body {
  font-family: 'Lato', sans-serif;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  outline: none !important;
}

/* HEADER */
header {
  background-color: #f6f9d0;
  color: #333;
  position: fixed;
  width: 100%;
  top:  0;
  height: 60px;
  z-index: 99;
}
.title-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
}
.back {
  text-decoration: none;
}

h1 {
  font-weight: 300;
  font-size:  18px;
  text-align: center;
  -webkit-flex: 1 1 275px;
  flex: 1 1 275px;
  margin-left: 20px;
  line-height: normal;
}
.title-container p {
  -webkit-flex: 1 1 85px;
  flex: 1 1 85px;
  border: 1px solid rgba(0,0,0,0.15);
  padding: 4px;
  background-color: rgba(0,0,0,0.08);
  border-radius: 4px;
  font-size: 0.8em;
}
@media (min-width: 474px) {
  h1 {
    font-size: 28px;
  } 
  .title-container p {
    font-size: 1em;
  }
}

/* VIZ */
#viz {
  max-width: 600px;
  margin: 52px auto 20px;
}
#legend-container, #map-container {
 position: relative; 
 height: 0; 
 width: 100%;
}

svg {
  position: absolute; 
  width: 100%;
  height: 100%;
  left: 0; 
  top: 0;
  font-family: 'Lato', Arial,"Trebuchet MS",sans-serif;
}

#selects-container {
  margin-top:  20px;
}
select {
  display: inline-block;
}

#legend-container canvas {
  position: absolute;
} 

#legend-container {
  margin: 20px 0 20px 20px;
}

#district-textinfo-container {
  margin:  0 16px;
}

#legend-axis .domain {
  display: none;
}
#legend-axis text {
  font-size: 19px;
}

#map-container {
 margin: 20px 0 0;
}
#city-districts {
  stroke:  #000;
  stroke-width: 1px;
}

#city-districts:hover {
  cursor:  pointer;
}
#table-container {
  margin: 20px 0 24px;
}

#table-container th {
  text-align: center;
}

#table-container td:nth-child(n+2) {
  text-align: right;
}

@media (min-width: 420px) {
  #legend-axis text {
    font-size: 18px;
  } 
}

@media (min-width: 600px) {
  #legend-axis text {
    font-size: 16px;
  }   
  #table-container td:nth-child(n+2) {
    padding-right: 8%;
  } 
}

@media (min-width: 800px) {
  #legend-axis text {
    font-size: 13px;
  } 
}

